'use client'

import { useState, useMemo } from 'react'
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { DatePicker } from '@/components/ui/date-picker'
import { Badge } from '@/components/ui/badge'
import { format, startOfDay, endOfDay, parseISO, startOfWeek, endOfWeek, startOfMonth, endOfMonth } from 'date-fns'
import { es } from 'date-fns/locale'
import { Trash2, Filter, FileSpreadsheet, FileText, ArrowLeft, ChevronRight } from 'lucide-react'
import * as XLSX from 'xlsx'
import jsPDF from 'jspdf'
import 'jspdf-autotable'

declare module 'jspdf' {
    interface jsPDF {
        autoTable: (options: any) => jsPDF;
    }
}

interface Viatico {
    id: string
    usuario_id: string
    fecha: string
    tipo: string
    monto: number | string
    descripcion: string
    url_onedrive?: string
    created_at: string
}

interface User {
    uid: string
    email: string
    displayName?: string
    role: string
}

interface ReportsViewProps {
    viaticos: Viatico[]
    users: User[]
    onDelete: (id: string) => Promise<void>
}

export default function ReportsView({ viaticos, users, onDelete }: ReportsViewProps) {
    const [startDate, setStartDate] = useState<Date | undefined>()
    const [endDate, setEndDate] = useState<Date | undefined>()
    const [groupBy, setGroupBy] = useState<'day' | 'week' | 'month'>('month')
    const [selectedUserId, setSelectedUserId] = useState<string | null>(null)

    // Filter Data by date range
    const filteredData = useMemo(() => {
        return viaticos.filter(v => {
            const vDate = parseISO(v.fecha)

            if (startDate) {
                const start = startOfDay(startDate)
                if (vDate < start) return false
            }
            if (endDate) {
                const end = endOfDay(endDate)
                if (vDate > end) return false
            }

            return true
        })
    }, [viaticos, startDate, endDate])

    // Aggregate by User and Period
    const userSummary = useMemo(() => {
        const groups: Record<string, Record<string, { period: string, total: number, count: number, items: Viatico[] }>> = {}

        filteredData.forEach(v => {
            const date = parseISO(v.fecha)
            let periodKey = ''
            let periodLabel = ''

            if (groupBy === 'day') {
                periodKey = format(date, 'yyyy-MM-dd')
                periodLabel = format(date, 'dd/MM/yyyy', { locale: es })
            } else if (groupBy === 'week') {
                const start = startOfWeek(date, { locale: es, weekStartsOn: 1 })
                const end = endOfWeek(date, { locale: es, weekStartsOn: 1 })
                periodKey = format(start, 'yyyy-ww')
                periodLabel = `${format(start, 'dd/MM')} - ${format(end, 'dd/MM/yyyy')}`
            } else {
                periodKey = format(date, 'yyyy-MM')
                periodLabel = format(date, 'MMMM yyyy', { locale: es })
            }

            if (!groups[v.usuario_id]) {
                groups[v.usuario_id] = {}
            }

            if (!groups[v.usuario_id][periodKey]) {
                groups[v.usuario_id][periodKey] = {
                    period: periodLabel,
                    total: 0,
                    count: 0,
                    items: []
                }
            }

            const monto = typeof v.monto === 'string' ? parseFloat(v.monto) : v.monto
            groups[v.usuario_id][periodKey].total += isNaN(monto) ? 0 : monto
            groups[v.usuario_id][periodKey].count += 1
            groups[v.usuario_id][periodKey].items.push(v)
        })

        // Convert to array format
        const result: { userId: string, userName: string, periods: { period: string, total: number, count: number, items: Viatico[] }[], grandTotal: number }[] = []

        Object.entries(groups).forEach(([userId, periods]) => {
            const periodsArray = Object.values(periods).sort((a, b) => b.total - a.total)
            const grandTotal = periodsArray.reduce((sum, p) => sum + p.total, 0)

            result.push({
                userId,
                userName: getUserName(userId),
                periods: periodsArray,
                grandTotal
            })
        })

        return result.sort((a, b) => b.grandTotal - a.grandTotal)
    }, [filteredData, groupBy])

    const getUserName = (uid: string) => {
        const u = users.find(user => user.uid === uid)
        return u ? (u.displayName || u.email) : uid
    }

    // Get selected user data
    const selectedUserData = useMemo(() => {
        if (!selectedUserId) return null
        return userSummary.find(u => u.userId === selectedUserId)
    }, [selectedUserId, userSummary])

    // Export to Excel
    const exportToExcel = () => {
        const dataToExport = selectedUserData
            ? selectedUserData.periods.flatMap(period =>
                period.items.map(v => ({
                    Usuario: selectedUserData.userName,
                    Fecha: format(parseISO(v.fecha), 'dd/MM/yyyy'),
                    Periodo: period.period,
                    Tipo: v.tipo,
                    Monto: typeof v.monto === 'string' ? parseFloat(v.monto) : v.monto,
                    Descripcion: v.descripcion
                }))
            )
            : userSummary.flatMap(user =>
                user.periods.map(period => ({
                    Usuario: user.userName,
                    Periodo: period.period,
                    'Cantidad Vi치ticos': period.count,
                    'Total (S/)': period.total
                }))
            )

        const ws = XLSX.utils.json_to_sheet(dataToExport)
        const wb = XLSX.utils.book_new()
        XLSX.utils.book_append_sheet(wb, ws, selectedUserData ? 'Detalle' : 'Resumen')
        XLSX.writeFile(wb, `Reporte_Viaticos_${format(new Date(), 'yyyyMMdd_HHmm')}.xlsx`)
    }

    // Export to PDF
    const exportToPDF = () => {
        const doc = new jsPDF()

        doc.text(`Reporte de Vi치ticos${selectedUserData ? ` - ${selectedUserData.userName}` : ''}`, 14, 15)
        doc.setFontSize(10)
        doc.text(`Generado: ${format(new Date(), 'dd/MM/yyyy HH:mm')}`, 14, 22)

        if (selectedUserData) {
            const tableData = selectedUserData.periods.flatMap(period =>
                period.items.map(v => [
                    format(parseISO(v.fecha), 'dd/MM/yyyy'),
                    period.period,
                    v.tipo,
                    `S/ ${Number(v.monto).toFixed(2)}`,
                    v.descripcion || '-'
                ])
            )

            doc.autoTable({
                head: [['Fecha', 'Periodo', 'Tipo', 'Monto', 'Desc.']],
                body: tableData,
                startY: 30,
            })
        } else {
            const tableData = userSummary.flatMap(user =>
                user.periods.map(period => [
                    user.userName,
                    period.period,
                    period.count,
                    `S/ ${period.total.toFixed(2)}`
                ])
            )

            doc.autoTable({
                head: [['Usuario', 'Periodo', 'Cant.', 'Total']],
                body: tableData,
                startY: 30,
            })
        }

        doc.save(`Reporte_Viaticos_${format(new Date(), 'yyyyMMdd_HHmm')}.pdf`)
    }

    return (
        <div className="space-y-6">
            <Card>
                <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                        <Filter className="h-5 w-5" />
                        Filtros de Reporte
                    </CardTitle>
                    <CardDescription>Personaliza la vista de los datos</CardDescription>
                </CardHeader>
                <CardContent>
                    <div className="grid gap-4 md:grid-cols-3">
                        <div className="space-y-2">
                            <label className="text-sm font-medium">Desde</label>
                            <DatePicker
                                date={startDate}
                                onSelect={setStartDate}
                                placeholder="Fecha inicio"
                            />
                        </div>
                        <div className="space-y-2">
                            <label className="text-sm font-medium">Hasta</label>
                            <DatePicker
                                date={endDate}
                                onSelect={setEndDate}
                                placeholder="Fecha fin"
                            />
                        </div>
                        <div className="space-y-2">
                            <label className="text-sm font-medium">Agrupar por</label>
                            <Select value={groupBy} onValueChange={(v: any) => setGroupBy(v)}>
                                <SelectTrigger>
                                    <SelectValue />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="day">Diario</SelectItem>
                                    <SelectItem value="week">Semanal</SelectItem>
                                    <SelectItem value="month">Mensual</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                    </div>
                </CardContent>
            </Card>

            <div className="flex justify-between items-center">
                {selectedUserId && (
                    <Button variant="outline" onClick={() => setSelectedUserId(null)}>
                        <ArrowLeft className="mr-2 h-4 w-4" />
                        Volver a resumen
                    </Button>
                )}
                <div className="flex gap-2 ml-auto">
                    <Button variant="outline" onClick={exportToExcel}>
                        <FileSpreadsheet className="mr-2 h-4 w-4 text-green-600" />
                        Excel
                    </Button>
                    <Button variant="outline" onClick={exportToPDF}>
                        <FileText className="mr-2 h-4 w-4 text-red-600" />
                        PDF
                    </Button>
                </div>
            </div>

            {!selectedUserId ? (
                <Card>
                    <CardHeader>
                        <CardTitle>Totales por Usuario</CardTitle>
                        <CardDescription>
                            Haz clic en un usuario para ver el detalle
                        </CardDescription>
                    </CardHeader>
                    <CardContent>
                        <div className="rounded-md border">
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableHead>Usuario</TableHead>
                                        <TableHead>Periodo</TableHead>
                                        <TableHead className="text-right">Cantidad</TableHead>
                                        <TableHead className="text-right">Total</TableHead>
                                        <TableHead className="text-right">Acci칩n</TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {userSummary.length === 0 ? (
                                        <TableRow>
                                            <TableCell colSpan={5} className="text-center py-8 text-muted-foreground">
                                                No hay datos para mostrar
                                            </TableCell>
                                        </TableRow>
                                    ) : (
                                        userSummary.flatMap(user =>
                                            user.periods.map((period, idx) => (
                                                <TableRow
                                                    key={`${user.userId}-${idx}`}
                                                    className="cursor-pointer hover:bg-muted/50"
                                                    onClick={() => setSelectedUserId(user.userId)}
                                                >
                                                    <TableCell className="font-medium">{user.userName}</TableCell>
                                                    <TableCell>{period.period}</TableCell>
                                                    <TableCell className="text-right">{period.count}</TableCell>
                                                    <TableCell className="text-right font-bold">
                                                        S/ {period.total.toLocaleString('es-PE', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                                                    </TableCell>
                                                    <TableCell className="text-right">
                                                        <ChevronRight className="h-4 w-4 inline" />
                                                    </TableCell>
                                                </TableRow>
                                            ))
                                        )
                                    )}
                                </TableBody>
                            </Table>
                        </div>
                    </CardContent>
                </Card>
            ) : (
                <Card>
                    <CardHeader>
                        <CardTitle>Detalle de {selectedUserData?.userName}</CardTitle>
                        <CardDescription>
                            Total: S/ {selectedUserData?.grandTotal.toLocaleString('es-PE', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                        </CardDescription>
                    </CardHeader>
                    <CardContent>
                        <div className="rounded-md border">
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableHead>Fecha</TableHead>
                                        <TableHead>Periodo</TableHead>
                                        <TableHead>Tipo</TableHead>
                                        <TableHead>Monto</TableHead>
                                        <TableHead>Descripci칩n</TableHead>
                                        <TableHead className="text-right">Acciones</TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {selectedUserData?.periods.flatMap(period =>
                                        period.items.map(viatico => (
                                            <TableRow key={viatico.id}>
                                                <TableCell>{format(parseISO(viatico.fecha), 'dd/MM/yyyy')}</TableCell>
                                                <TableCell><Badge variant="secondary">{period.period}</Badge></TableCell>
                                                <TableCell><Badge variant="outline">{viatico.tipo}</Badge></TableCell>
                                                <TableCell>
                                                    S/ {Number(viatico.monto).toLocaleString('es-PE', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}
                                                </TableCell>
                                                <TableCell className="max-w-xs truncate">{viatico.descripcion || '-'}</TableCell>
                                                <TableCell className="text-right">
                                                    <Button
                                                        variant="ghost"
                                                        size="sm"
                                                        className="text-red-500 hover:text-red-700 hover:bg-red-50"
                                                        onClick={() => onDelete(viatico.id)}
                                                    >
                                                        <Trash2 className="h-4 w-4" />
                                                    </Button>
                                                </TableCell>
                                            </TableRow>
                                        ))
                                    )}
                                </TableBody>
                            </Table>
                        </div>
                    </CardContent>
                </Card>
            )}
        </div>
    )
}
