'use client'

// HINT: Make sure to install sweetalert2: npm install sweetalert2
import Swal from 'sweetalert2'
import { useToast } from "@/lib/use-toast"
import { useState, useEffect } from 'react'
import { useAuth } from '@/contexts/AuthContext'
import { getAllViaticos, getAllUsers, setUserRole as apiSetUserRole, getCurrentUser, cleanupAnonymousUsers, setUserStatus, setUserCreateFolder, deleteUser, deleteViatico } from '@/services/api'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Switch } from '@/components/ui/switch'
import { Loader2, Users, Receipt, Shield, Crown, RefreshCw, DollarSign, ExternalLink, Trash2, Ban, CheckCircle2 } from 'lucide-react'
import Layout from '@/components/Layout'
import RoleGuard from '@/components/RoleGuard'
import ReportsView from '@/components/admin/ReportsView'
import LoadingOverlay from '@/components/LoadingOverlay'
import { format } from 'date-fns'
import { es } from 'date-fns/locale'

interface Viatico {
  id: string
  usuario_id: string
  fecha: string
  tipo: string
  monto: number | string
  descripcion: string
  created_at: string
}

interface User {
  uid: string
  email: string
  displayName?: string
  role: string
  estado?: string
  crear_carpeta?: boolean
}

export default function AdminPage() {
  const { user } = useAuth()
  const { toast } = useToast()
  const [viaticos, setViaticos] = useState<Viatico[]>([])
  const [users, setUsers] = useState<User[]>([])
  const [userRole, setUserRole] = useState<string | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const [updating, setUpdating] = useState<string | null>(null)
  const [globalLoading, setGlobalLoading] = useState(false)
  const [loadingMessage, setLoadingMessage] = useState('')
  const [oneDriveUrl, setOneDriveUrl] = useState<string | null>(null)
  const superAdminEmail = process.env.NEXT_PUBLIC_SUPER_ADMIN_EMAIL;
  const isSuperAdmin = user?.email?.toLowerCase() === superAdminEmail?.toLowerCase();

  useEffect(() => {
    if (user) {
      loadData()
    }
  }, [user])

  async function loadData() {
    setLoading(true)
    setError('')
    try {
      // Cargar todo en paralelo para m치xima velocidad
      const [viaticosData, usersData, userData] = await Promise.all([
        getAllViaticos(),
        getAllUsers(),
        getCurrentUser()
      ])

      // Actualizar rol del usuario desde userData
      setUserRole(userData?.user?.role || null)

      setViaticos(viaticosData?.viaticos || [])

      // Usar count si est치 disponible, sino usar users.length
      const userCount = usersData?.count !== undefined ? usersData.count : (usersData?.users || []).length

      // Filtrar super_admin del array de usuarios para la tabla si es necesario
      const filteredUsers = (usersData?.users || []).filter((u: User) => {
        // Mantener todos los usuarios en la tabla, pero el conteo ya est치 filtrado
        return u.uid !== 'anonymous' && u.uid !== 'dev-user' && !u.uid.startsWith('dev-')
      })

      setUsers(filteredUsers)

      // Actualizar el conteo de usuarios (sin super_admin)
      setUserCount(userCount)
    } catch (e) {
      setError('Error al cargar datos: ' + (e as Error).message)
    } finally {
      setLoading(false)
    }
  }

  async function handleRoleChange(uid: string, newRole: string) {
    setGlobalLoading(true)
    setLoadingMessage('Actualizando rol...')
    setUpdating(uid)
    try {
      await apiSetUserRole(uid, newRole)
      toast({ title: "Rol actualizado" })
      await loadData()
    } catch (e) {
      toast({ title: "Error", description: "Error al actualizar rol", variant: "destructive" })
    } finally {
      setUpdating(null)
      setGlobalLoading(false)
    }
  }



  async function handleStatusChange(uid: string, nuevoEstado: 'activo' | 'inactivo') {
    setGlobalLoading(true)
    setLoadingMessage('Actualizando estado...')
    try {
      await setUserStatus(uid, nuevoEstado)
      toast({ title: "Estado actualizado" })
      await loadData()
    } catch (e) {
      toast({ title: "Error", description: "Error al actualizar estado", variant: "destructive" })
    } finally {
      setGlobalLoading(false)
    }
  }


  async function handleCreateFolderChange(uid: string, crearCarpeta: boolean) {
    setGlobalLoading(true)
    setLoadingMessage('Actualizando configuraci칩n...')
    try {
      await setUserCreateFolder(uid, crearCarpeta)
      toast({ title: "Configuraci칩n actualizada" })
      await loadData()
    } catch (e) {
      toast({ title: "Error", description: "Error al actualizar flag de carpeta", variant: "destructive" })
    } finally {
      setGlobalLoading(false)
    }
  }

  async function handleDeleteUser(uid: string, email: string) {
    const result = await Swal.fire({
      title: 'Eliminar usuario',
      html: `
        <div style="font-size:16px; text-align:left; line-height:1.6; padding: 5px 0;">
          <p>쮼st치s seguro de que deseas eliminar al usuario <b>${email}</b>? Esto tambi칠n eliminar치:</p>
          <ul style="margin-left:18px; padding-left:0;">
            <li>游듹勇 Su registro en la base de datos</li>
            <li>游늯 Todos sus vi치ticos</li>
            <li>驕勇 Su carpeta en OneDrive</li>
          </ul>
          <p style="margin-top:12px; color:#d9534f; font-weight:bold;">
            Esta acci칩n NO se puede deshacer.
          </p>
        </div>
      `,
      icon: 'warning',
      iconColor: '#e63946',
      showCancelButton: true,
      confirmButtonText: 'Eliminar usuario',
      cancelButtonText: 'Cancelar',
      confirmButtonColor: '#d62828',
      cancelButtonColor: '#6c757d',
      reverseButtons: true,
      focusCancel: true,
      width: 500,
      customClass: {
        popup: 'rounded-xl',
        confirmButton: 'rounded-md px-4 py-2',
        cancelButton: 'rounded-md px-4 py-2',
      },
    })

    if (!result.isConfirmed) return

    setGlobalLoading(true)
    setLoadingMessage('Eliminando usuario y sus datos...')
    try {
      await deleteUser(uid)

      setGlobalLoading(false)
      await Swal.fire({
        title: 'Usuario eliminado',
        text: 'El usuario fue eliminado correctamente.',
        icon: 'success',
        confirmButtonColor: '#2a9d8f',
      })

      await loadData()
    } catch (e) {
      setGlobalLoading(false)
      await Swal.fire({
        title: 'Error',
        text: 'Error al eliminar usuario: ' + (e as Error).message,
        icon: 'error',
        confirmButtonColor: '#d62828',
      })
    }
  }



  async function handleCleanupAnonymous() {
    const result = await Swal.fire({
      title: '쯃impiar usuarios an칩nimos?',
      text: 'Se eliminar치n TODOS los usuarios anonymous y sus vi치ticos. Esta acci칩n no se puede deshacer.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'S칤, limpiar',
      cancelButtonText: 'Cancelar',
      confirmButtonColor: '#d33',
    })

    if (!result.isConfirmed) return

    try {
      const data = await cleanupAnonymousUsers()
      Swal.fire(
        'Completado',
        `Usuarios eliminados: ${data.deletedUsers}<br>Vi치ticos eliminados: ${data.deletedViaticos}`,
        'success'
      )
      await loadData()
    } catch (e) {
      Swal.fire('Error', (e as Error).message || 'Error ejecutando limpieza', 'error')
    }
  }

  async function handleDeleteViatico(id: string) {
    const result = await Swal.fire({
      title: '쮼liminar vi치tico?',
      text: 'Esta acci칩n eliminar치 el registro y los archivos asociados de OneDrive. No se puede deshacer.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'S칤, eliminar',
      cancelButtonText: 'Cancelar',
      confirmButtonColor: '#d33',
    })

    if (!result.isConfirmed) return

    setGlobalLoading(true)
    setLoadingMessage('Eliminando vi치tico...')
    try {
      await deleteViatico(id)
      setGlobalLoading(false)
      await Swal.fire('Eliminado', 'El vi치tico ha sido eliminado.', 'success')
      await loadData()
    } catch (e) {
      setGlobalLoading(false)
      await Swal.fire('Error', (e as Error).message || 'Error al eliminar vi치tico', 'error')
    }
  }


  const totalViaticos = viaticos.length
  const totalMonto = viaticos.reduce((sum, v) => {
    const monto = typeof v.monto === 'string' ? parseFloat(v.monto) : v.monto
    return sum + (isNaN(monto) ? 0 : monto)
  }, 0)

  // Usar userCount en lugar de users.length
  const [userCount, setUserCount] = useState(0)

  return (
    <RoleGuard allowedRoles={['admin', 'super_admin']}>
      <Layout>
        <LoadingOverlay isLoading={globalLoading} message={loadingMessage} />
        <div className="space-y-8">
          <div>
            <div className="flex items-center gap-2 mb-2">
              {userRole === 'super_admin' ? (
                <Crown className="h-6 w-6 text-yellow-500" />
              ) : (
                <Shield className="h-6 w-6 text-blue-500" />
              )}
              <h1 className="text-3xl font-bold tracking-tight">
                Panel de Administraci칩n
              </h1>
            </div>
            <p className="text-muted-foreground">
              Gestiona todos los vi치ticos y usuarios del sistema
            </p>
          </div>

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <div className="grid gap-4 md:grid-cols-3">
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Total Vi치ticos</CardTitle>
                <Receipt className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{totalViaticos}</div>
                <p className="text-xs text-muted-foreground">
                  Registros en el sistema
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Total Monto</CardTitle>
                <DollarSign className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">
                  S/ {totalMonto.toLocaleString('es-PE', { minimumFractionDigits: 2, maximumFractionDigits: 2 })} {/* CORREGIDO: Cambiar $ a S/ y es-MX a es-PE */}
                </div>
                <p className="text-xs text-muted-foreground">
                  Suma de todos los vi치ticos
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">Usuarios</CardTitle>
                <Users className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{userCount}</div>
                <p className="text-xs text-muted-foreground">
                  Usuarios registrados
                </p>
              </CardContent>
            </Card>
          </div>

          <Tabs defaultValue="viaticos" className="space-y-4">
            <div className="flex items-center justify-between">
              <TabsList>
                <TabsTrigger value="viaticos">Vi치ticos</TabsTrigger>
                <TabsTrigger value="usuarios">Usuarios</TabsTrigger>
              </TabsList>
              <div className="flex items-center gap-2">
                <Button onClick={loadData} variant="outline" size="sm">
                  <RefreshCw className="h-4 w-4 mr-2" />
                  Actualizar
                </Button>
              </div>
            </div>

            <TabsContent value="viaticos" className="space-y-4">
              <ReportsView
                viaticos={viaticos}
                users={users}
                onDelete={handleDeleteViatico}
              />
            </TabsContent>

            <TabsContent value="usuarios" className="space-y-4">
              <Card>
                <CardHeader className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-3">
                  <CardTitle>Gesti칩n de Usuarios</CardTitle>
                  <div className="flex gap-2">
                    {isSuperAdmin && (
                      <Button onClick={handleCleanupAnonymous} variant="destructive" size="sm">
                        Limpiar Anonymous
                      </Button>
                    )}
                    <Button onClick={loadData} disabled={loading} size="sm">
                      <RefreshCw className={`mr-2 h-4 w-4 ${loading ? 'animate-spin' : ''}`} />
                      Actualizar
                    </Button>
                  </div>
                </CardHeader>
                <CardContent>
                  {users.length === 0 ? (
                    <div className="text-center py-8 text-muted-foreground">
                      No hay usuarios registrados
                    </div>
                  ) : (
                    <div className="rounded-md border">
                      <Table>
                        <TableHeader>
                          <TableRow>
                            <TableHead>Usuario</TableHead>
                            <TableHead>Email</TableHead>
                            <TableHead>Rol Actual</TableHead>
                            <TableHead>Estado</TableHead>
                            <TableHead>Crear Carpeta</TableHead>
                            <TableHead>Cambiar Rol</TableHead>
                            <TableHead className="text-right">Acciones</TableHead>
                          </TableRow>
                        </TableHeader>
                        <TableBody>
                          {users.map((u) => (
                            <TableRow key={u.uid}>
                              <TableCell>
                                {u.displayName || u.email.split('@')[0]}
                              </TableCell>
                              <TableCell>{u.email}</TableCell>
                              <TableCell>
                                <Badge
                                  variant={
                                    u.role === 'super_admin'
                                      ? 'default'
                                      : u.role === 'admin'
                                        ? 'secondary'
                                        : 'outline'
                                  }
                                >
                                  {u.role === 'super_admin' ? 'Super Admin' : u.role === 'admin' ? 'Admin' : 'Usuario'}
                                </Badge>
                              </TableCell>
                              <TableCell>
                                <div className="flex items-center gap-2">
                                  <Badge variant={u.estado === 'activo' ? 'default' : 'destructive'}>
                                    {u.estado === 'activo' ? (
                                      <>
                                        <CheckCircle2 className="h-3 w-3 mr-1" />
                                        Activo
                                      </>
                                    ) : (
                                      <>
                                        <Ban className="h-3 w-3 mr-1" />
                                        Inactivo
                                      </>
                                    )}
                                  </Badge>
                                  {userRole === 'super_admin' && u.role !== 'super_admin' && (
                                    <Select
                                      value={u.estado || 'activo'}
                                      onValueChange={(newEstado) => handleStatusChange(u.uid, newEstado as 'activo' | 'inactivo')}
                                    >
                                      <SelectTrigger className="w-32">
                                        <SelectValue />
                                      </SelectTrigger>
                                      <SelectContent>
                                        <SelectItem value="activo">Activo</SelectItem>
                                        <SelectItem value="inactivo">Inactivo</SelectItem>
                                      </SelectContent>
                                    </Select>
                                  )}
                                </div>
                              </TableCell>
                              <TableCell>
                                <div className="flex items-center gap-2">
                                  <Switch
                                    checked={u.crear_carpeta !== false}
                                    onCheckedChange={(checked) => handleCreateFolderChange(u.uid, checked)}
                                    disabled={userRole === 'admin' && (u.role === 'admin' || u.role === 'super_admin')}
                                  />
                                  <span className="text-sm text-muted-foreground">
                                    {u.crear_carpeta !== false ? 'S칤' : 'No'}
                                  </span>
                                </div>
                              </TableCell>
                              <TableCell>
                                <Select
                                  value={u.role}
                                  onValueChange={(newRole) => handleRoleChange(u.uid, newRole)}
                                  disabled={userRole !== 'super_admin' || u.role === 'super_admin'}
                                // disabled={updating === u.uid || (userRole !== 'super_admin' && u.role === 'super_admin')}
                                >
                                  <SelectTrigger className="w-40">
                                    {updating === u.uid ? (
                                      <Loader2 className="h-4 w-4 animate-spin" />
                                    ) : (
                                      <SelectValue />
                                    )}
                                  </SelectTrigger>
                                  <SelectContent>
                                    <SelectItem value="usuario">Usuario</SelectItem>
                                    <SelectItem value="admin">Admin</SelectItem>
                                    {userRole === 'super_admin' && (
                                      <SelectItem value="super_admin">Super Admin</SelectItem>
                                    )}
                                  </SelectContent>
                                </Select>
                              </TableCell>
                              <TableCell className="text-right">
                                {userRole === 'super_admin' && u.role !== 'super_admin' && (
                                  <Button
                                    variant="destructive"
                                    size="sm"
                                    onClick={() => handleDeleteUser(u.uid, u.email)}
                                  >
                                    <Trash2 className="h-4 w-4" />
                                  </Button>
                                )}
                              </TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </Table>
                    </div>
                  )}
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>
        </div>
      </Layout >
    </RoleGuard >
  )
}

